import React from "react";
import Spinner from "react-spinner-material";

import { colors } from "@shared-colors/colors";

import Text from "../components/Text";
import View from "../components/View";

type IButtonProps = {
  title: string;
  onPress: () => void;
  colorVariant?:
    | "secondary"
    | "error"
    | "warning"
    | "success"
    | "primary"
    | "light"
    | "dark";
  className?: string;
  style?: any;
  isLoading?: boolean;
};

function Button(props: IButtonProps) {
  const colorVariantStyle =
    props.colorVariant && styles[props.colorVariant]
      ? styles[props.colorVariant]
      : styles.secondary;

  return (
    <button
      className={props.className}
      style={{ ...styles.button, ...colorVariantStyle }}
      onClick={props.onPress}
    >
      {props.isLoading ? (
        <View style={styles.loaderContainer}>
          <Spinner radius={16} color={colors.primary} stroke={2} visible />
        </View>
      ) : (
        <Text
          style={styles.text}
          weight="bold"
          colorVariant="light"
          type="heading1"
        >
          {props.title}
        </Text>
      )}
    </button>
  );
}

const styles = {
  primary: {
    backgroundColor: colors.primary,
  },
  secondary: {
    backgroundColor: colors.secondary,
  },
  accent: {
    backgroundColor: colors.accent,
  },
  error: {
    backgroundColor: colors.error,
  },
  warning: {
    backgroundColor: colors.warning,
  },
  success: {
    backgroundColor: colors.success,
  },
  light: {
    backgroundColor: "#fff",
  },
  dark: {
    color: colors.background.dark,
  },
  loaderContainer: {
    justifyContent: "center",
    alignItems: "center",
    flex: 1,
    display: "flex",
  },
  button: {
    width: "100%",
    display: "flex",
    justifyContent: "start",
    alignItems: "center",
    backgroundColor: colors.secondary,
    cursor: "pointer",

    border: "none",
    outline: "none",
    /* borderRadius: 10, */
    /* padding: "13px 0", */
    maxHeight: 48,
    borderRadius: 20,
    padding: 8,
  },
  text: {
    color: colors.text.white,
    fontWeight: 700,
    marginLeft: 20,
  },
};

export default Button;
