import React, { useEffect, useState, useContext, useCallback } from "react";
import { useHistory, useLocation } from "react-router-dom";
import queryString from "query-string";
import { AlertContext } from "../components/Alerts";
import firebase from "firebase/app";
import "firebase/auth";
import "firebase/database";
import Spinner from "react-spinner-material";

import { colors } from "@shared-colors/colors";
import View from "@shared-components/View";
import Logo from "@shared-components/Logo";

import { useNotification } from "../hooks/notifications.hooks";
import AccountVerification from "./AccountVerification.page";
import CurrentUserContextProvider from "../context/CurrentUser.context";

const Initializing = () => {
  const location = useLocation();
  const { dispatch } = useContext(AlertContext);
  const history = useHistory();
  const [isInitializing, setIsInitializing] = useState(true);
  const [notificationId, setNotificationId] = useState<any>();
  const notification = useNotification(notificationId);
  const currentUser = firebase.auth().currentUser as
    | Teams.CurrentUser
    | undefined;

  const setError = useCallback(
    (error: any) => {
      if (error.message) {
        dispatch({ type: "addAlert", payload: error });
      } else if (typeof error === "string") {
        dispatch({ type: "addAlert", payload: { message: error } });
      } else {
        dispatch({ type: "addAlert", payload: { message: "Unknown error" } });
      }
    },
    [dispatch]
  );

  useEffect(() => {
    let isCancelled = false;
    if (!isCancelled) {
      const { notificationId } = queryString.parse(location.search);
      try {
        if (notificationId) {
          setNotificationId(notificationId);
        }
      } catch (error) {
        setError(error);
      } finally {
        setIsInitializing(false);
      }
    }

    return () => {
      isCancelled = false;
    };
  }, [location.search, setError]);

  const redirectBasedOnUrlQuery = useCallback(async () => {
    const { from } = queryString.parse(location.search);
    if (from === "sms") {
      history.push(`/sms-auth?notificationId=${notification.id}`);
    } else if (from === "email") {
      history.push(`/email-link-auth?notificationId=${notification.id}`);
    } else {
      throw new Error("initializing/fatal-error");
    }
  }, [history, notification, location]);

  useEffect(() => {
    const initialise = async () => {
      if (notification) {
        if (!currentUser) {
          redirectBasedOnUrlQuery();
        }
      }
    };

    try {
      initialise();
    } catch (error) {
      setError(error);
    }
  }, [notification, history, setError, redirectBasedOnUrlQuery, currentUser]);

  if (isInitializing || notification === undefined) {
    return (
      <View
        className="MQ-landing"
        flexDirection="column"
        style={styles.container}
      >
        <View style={{ justifyContent: "center" }}>
          <Logo scale={0.25} />
        </View>
        <View flexDirection="column" style={styles.confirmationContainer}>
          <Spinner radius={100} color={colors.secondary} stroke={2} visible />
        </View>
      </View>
    );
  }

  if (!isInitializing && notification === null) {
    return (
      <View
        className="MQ-landing"
        flexDirection="column"
        style={styles.container}
      >
        <View style={{ justifyContent: "center" }}>
          <Logo scale={0.25} />
        </View>

        <View flexDirection="column" style={styles.confirmationContainer}>
          <View style={styles.textContainer}>
            An error occurred when fetching data, please try again.
          </View>
        </View>
      </View>
    );
  }

  if (currentUser) {
    return (
      <CurrentUserContextProvider currentUser={currentUser}>
        <AccountVerification />
      </CurrentUserContextProvider>
    );
  }

  return (
    <View
      className="MQ-landing"
      flexDirection="column"
      style={styles.container}
    >
      <View style={{ justifyContent: "center" }}>
        <Logo scale={0.25} />
      </View>

      <View flexDirection="column" style={styles.confirmationContainer}>
        <View style={styles.textContainer} />
      </View>
    </View>
  );
};

const styles: any = {
  container: {
    width: "100%",
    height: "70vh",
    justifyContent: "space-between",
    margin: "0 20px",
  },
  confirmationContainer: {
    flex: 1,
    alignItems: "center",
    justifyContent: "center",
    justifyItems: "center",
  },
  textContainer: {
    marginTop: "60px",
    width: "84%",
    alignSelf: "center",
    justifyContent: "center",
    textAlign: "center",
  },
};

export default Initializing;
