import _ from "lodash";
import React, { useEffect } from "react";
import { useDispatch, useSelector, shallowEqual } from "react-redux";
import moment, { Moment } from "moment";

import { FormContext } from "@shared/context/Form.context";
import ErrorsList from "@shared/components/Errors";
import { useValidation } from "@shared//hooks/validation.hooks";
import { FormActions } from "@shared/constants/forms.constants";

const serializeValue: Archetypes.serializeTimestamp = ({
  created,
  account,
  value,
}) => ({
  created,
  account,
  timestamp: value,
});

export const createTimestamp = ({
  valueSelector,
  valueDisplaySelector,
  serialize = serializeValue,
}: {
  valueSelector: any;
  valueDisplaySelector: any;
  serialize?: Archetypes.serializeTimestamp;
}) => (Component: any) => {
  const TimestampModal = ({
    path,
    validations,
    title,
    subtitle,
    defaultValue,
    isDisabled,
    disableFuture,
    disablePast,
    account,
  }: any) => {
    const dispatch = useDispatch();
    const formContext = React.useContext(FormContext);

    const value = useSelector((state: any) => {
      return valueSelector(_.get(state.form, formContext) ?? {}, path);
    }, shallowEqual);

    const contextState = useSelector((state: any) => {
      return _.get(state.form, formContext) ?? {};
    }, shallowEqual);

    const validationResponses = useValidation(value, validations);

    useEffect(() => {
      if (validationResponses.length >= 1) {
        if (!isDisabled) {
          dispatch({
            type: FormActions.ADD_ENTRY_ERRORS,
            payload: {
              path,
              formContext,
              errors: validationResponses,
            },
          });
        } else if (isDisabled && !isDisabled(contextState)) {
          dispatch({
            type: FormActions.ADD_ENTRY_ERRORS,
            payload: {
              path,
              formContext,
              errors: validationResponses,
            },
          });
        } else {
          dispatch({
            type: FormActions.RESET_ENTRY_ERRORS,
            payload: {
              path,
              formContext,
            },
          });
        }
      }
    }, [validationResponses, contextState]);

    useEffect(() => {
      if (isDisabled && isDisabled(contextState) && value) {
        dispatch({
          payload: {
            path,
            formContext,
          },
          type: FormActions.RESET_ENTRY,
        });
      }
    }, [contextState]);

    const onChange = (newValue: Moment) => {
      const newSerialized = serialize({
        created: moment().valueOf(),
        account,
        value: newValue.valueOf(),
      });

      dispatch({
        payload: {
          path,
          formContext,
          serialized: newSerialized,
        },
        type: FormActions.ADD_ENTRY,
      });
    };

    useEffect(() => {
      if (!value && defaultValue) {
        const newSerialized = serialize({
          created: moment().valueOf(),
          account,
          value: defaultValue,
        });

        dispatch({
          payload: {
            path,
            formContext,
            serialized: newSerialized,
          },
          type: FormActions.ADD_ENTRY,
        });
      }
    }, []);

    const displayValue = useSelector((state: any) => {
      return valueDisplaySelector(state.form[formContext] ?? {}, path);
    });

    return (
      <>
        <Component
          pips={true}
          onChange={onChange}
          value={value}
          title={displayValue || title}
          subtitle={displayValue && subtitle}
          isSelected={value !== undefined}
          disableFuture={disableFuture}
          disablePast={disablePast}
          disabled={isDisabled && isDisabled(contextState)}
        />
        <ErrorsList formContext={formContext} path={path} />
      </>
    );
  };

  return TimestampModal;
};
