import React from "react";
import styled from "styled-components";

import Text from "@shared-components/Text";
import { colors } from "@shared-colors/colors";
import Pressable from "@shared-components/Pressable";
import View from "@shared-components/View";

type ItemValue = {
  value: string;
  display: string;
  text?: string;
  sub?: string;
  colorVariant?: string;
};

type Props = {
  item: ItemValue;
  onPress: (value: string) => void;
  index: number;
  layout?: "list-horizontal" | "list-vertical" | "grid";
  isSelected: boolean;
  itemsNum: number;
  width?: number;
  disabled?: boolean;
};

type StyleProps = {
  item: ItemValue;
  isSelected: boolean;
  disabled?: boolean;
};

const StyledPressable = styled(Pressable)<StyleProps>`
  background-color: ${(props) =>
    !props.isSelected
      ? colors.background.grey
      : props.item?.colorVariant
      ? colors.background[props.item.colorVariant]
      : colors.accent};
  flex: 1;
  margin: 2px;
  align-items: center;
  justify-content: center;
  padding: 12px;
  border-radius: 5px;
  opacity: ${(props) => (props.disabled ? 0.5 : 1)};
`;

const Item: React.FC<Props> = ({
  item,
  onPress,
  index,
  layout,
  isSelected,
  itemsNum,
  width,
  disabled,
}: Props) => {
  return (
    <StyledPressable
      item={item}
      isSelected={isSelected}
      layout={layout}
      isFirst={index === 0}
      isLast={index === itemsNum - 1}
      width={width}
      onPress={() => onPress(item.value)}
      disabled={disabled}
    >
      <View style={{ alignItems: "center", justifyContent: "center" }}>
        <Text weight="regular" isSelected={isSelected} textAlign="center">
          {item.display}
        </Text>
        {item.text && (
          <Text weight="light" isSelected={isSelected} textAlign="center">
            {item.text}
          </Text>
        )}

        {item.sub && (
          <Text weight="light" isSelected={isSelected} textAlign="center">
            {item.sub}
          </Text>
        )}
      </View>
    </StyledPressable>
  );
};

export default Item;
