import React, { useEffect, useState } from "react";
import { Provider } from "react-redux";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect,
  useHistory,
} from "react-router-dom";
import firebase from "firebase/app";
import "firebase/auth";
import "firebase/database";
import Spinner from "react-spinner-material";
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css";

import { colors } from "@shared-colors/colors";
import LandingPage from "./pages/Landing.page";
import SubmitConfirmation from "./pages/Submit.page";
import ClinicianInvitation from "./pages/ClinicianInvitation.page";
import EmailLinkAuthPage from "./pages/EmailLinkAuth.page";
import SmsAuthPage from "./pages/SmsAuth.page";
import HomePage from "./pages/Home.page";
import InitializingPage from "./pages/Initializing.page";
import EmailLinkCheckPage from "./pages/EmailLinkCheck.page";
import ModalDeclined from "./pages/ModalDeclined.page";

import { AlertContextWrapper } from "./components/Alerts";
import "./style.css";
import store from "./reducers";

import CONFIG from "./config";

const firebaseConfig = {
  apiKey: CONFIG.API_KEY,
  authDomain: CONFIG.AUTH_DOMAIN,
  databaseURL: CONFIG.DATABASE_URL,
  projectId: CONFIG.PROJECT_ID,
  storageBucket: CONFIG.STORAGE_BUCKET,
  messagingSenderId: CONFIG.MESSAGING_SENDER_ID,
  appId: CONFIG.APP_ID,
  measurementId: CONFIG.MEASUREMENT_ID,
};

const firesbaseApp = firebase.initializeApp(firebaseConfig);
firebase.auth().useDeviceLanguage();

if (process.env.REACT_APP_STAGE === "debug") {
  firesbaseApp.firestore().settings({
    host: "localhost:8080",
    ssl: false,
  });
}

const PrivateRoute: React.FC<{ path: string }> = ({ children, ...rest }) => {
  const [currentUser, setCurrentUser] = useState(firebase.auth().currentUser);
  const [isLoading, setIsLoading] = useState(true);
  const history = useHistory();

  useEffect(() => {
    if (firebase.auth().currentUser) {
      setCurrentUser(firebase.auth().currentUser);
      setIsLoading(false);
    } else {
      firebase.auth().onAuthStateChanged(function (user) {
        if (user) {
          setCurrentUser(user);
          setIsLoading(false);
        } else {
          setIsLoading(false);
        }
      });
    }
  }, [history]);

  if (isLoading) {
    return <Spinner radius={100} color={colors.secondary} stroke={2} visible />;
  }

  return (
    <Route
      {...rest}
      render={({ location }) =>
        currentUser ? (
          children
        ) : (
          <Redirect
            to={{
              pathname: "/",
              state: { from: location },
            }}
          />
        )
      }
    />
  );
};

const App: React.FC<unknown> = () => {
  return (
    <Provider store={store}>
      <div id="recaptchaVerifier" />
      <AlertContextWrapper>
        <div style={styles.container}>
          <Router>
            <Switch>
              <Route exact path="/">
                <LandingPage />
              </Route>
              <Route path="/initialise">
                <InitializingPage />
              </Route>
              <Route path="/clinician-invitation">
                <ClinicianInvitation />
              </Route>
              <Route path="/email-link-auth">
                <EmailLinkAuthPage />
              </Route>
              <Route path="/covid-email-link">
                <EmailLinkCheckPage />
              </Route>
              <Route path="/sms-auth">
                <SmsAuthPage />
              </Route>
              <PrivateRoute path="/home">
                <HomePage />
              </PrivateRoute>
              <Route path="/submit-confirm">
                <SubmitConfirmation />
              </Route>
              <PrivateRoute path="/modal-declined">
                <ModalDeclined />
              </PrivateRoute>
            </Switch>
          </Router>
        </div>
      </AlertContextWrapper>
    </Provider>
  );
};

const styles = {
  public: {
    backgroundColor: colors.primary,
    flex: 1,
  },
  protected: {
    backgroundColor: "#c2c2c2",
    flex: 1,
  },
  container: {
    backgroundColor: colors.primary,
    alignItems: "center",
    justifyContent: "center",
    flex: 1,
    display: "flex",
    minHeight: "100%",
  },
};

export default App;
