import React from "react";
import RModal from "react-modal";

import firebase from "firebase/app";

import { useHistory } from "react-router-dom";

import Button from "./Button";
import View from "./View";
import Text from "./Text";

const customStyles: any = {
  content: {
    top: "50%",
    left: "50%",
    right: "auto",
    bottom: "auto",
    marginRight: "-50%",
    transform: "translate(-50%, -50%)",
  },
  overlay: {
    backgroundColor: "rgba(0, 0, 0, 0.2)",
  },
};

const TermConditionModal = (props: any) => {
  const currentUser = firebase
    .auth()
    .currentUser?.toJSON() as Teams.CurrentUser;
  const [modalIsOpen, setIsOpen] = React.useState(props.isVisible);
  const history = useHistory();

  const onDeclineTermCondition = () => {
    history.push("/modal-declined");
  };

  const confirmTermAndCondition = () => {
    const newDocumentRef = firebase
      .firestore()
      .collection("Accounts")
      .doc(currentUser?.uid);

    newDocumentRef.update({
      accpetedTsAndCs: true,
    });
    setIsOpen(false);
  };

  return (
    <>
      <RModal isOpen={modalIsOpen} style={{ ...customStyles }}>
        <View style={{ display: " flex " }} flexDirection="column">
          <View
            flexDirection="column"
            style={{
              height: 200,
              width: "90vw",
              overflowY: "scroll",
            }}
            className="MQ-landing margin"
          >
            <Text>
              This app is provided by ScribePro Ltd. We are committed to
              ensuring the security and protection of the personal information
              that we process, and to provide a compliant and consistent
              approach to data protection. The following data is collected by
              the ScribePro app:
              <br /> <br />- Personal information limited to basic identify and
              contact information (name, date of birth) <br />- Medical data
              limited to injuries and illness <br />- Training data limited to
              activities, timings, surfaces and equipment <br />
              This data is collected by your Club and its authorised medical
              professionals for the purposes of documenting your injuries and
              illnesses, supporting clinicians as they provide treatment, and
              for use in improving care and recovery through injury research.
              <br />
              <br />
              As a data processor ScribePro Limited do not use your data other
              than in relation to supporting the app and we will not market to
              you or share your data other than as set out below. The Data
              Controller for data protection purposes is the Club or
              organisation forwhom you play and their privacy policy will apply
              to the collection and use of the data you provide.
              <br />
              <br />
              Please note we will share some aggregated data with third parties.
              Data sharing is limited to recognised sporting and medical bodies.
              The data shared is restricted to medical and training data for the
              express purpose of research into sporting injury and all data
              shared is anonymised. You can request to view your data or have it
              removed from our database at any point by contacting
              hello@scribepro.co. By signing the box below you are agreeing with
              our privacy statement which can be found on our website
              www.scribepro.co and give ScribePro permission to collect and
              share the data as described above and in that.
            </Text>
          </View>
          <View
            flexDirection="row"
            style={{
              display: "flex",
              marginTop: 20,
              justifyContent: "space-between",
            }}
          >
            <View style={{ flex: 1, marginRight: 8 }}>
              <Button
                colorVariant="error"
                title="Decline"
                className="margin"
                onPress={onDeclineTermCondition}
              />
            </View>
            <View style={{ flex: 1, marginLeft: 8 }}>
              <Button
                colorVariant="secondary"
                title="Confirm"
                className="margin"
                onPress={confirmTermAndCondition}
              />
            </View>
          </View>
        </View>
      </RModal>
    </>
  );
};

export default TermConditionModal;
