import React, { useState, useEffect } from "react";
import _ from "lodash";

import { colors } from "@shared-colors/colors";

type Props = {
  value?: string | number;
  placeholder: string;
  onChange: (_value?: string | number) => void;
};

const TextInput: React.FC<Props> = (props) => {
  const debounce = _.debounce(props.onChange, 500);
  const [value, onChangeValue] = useState(props.value ?? "");

  useEffect(() => {
    if (props.value !== value) {
      debounce(value);
    }

    return () => debounce.cancel();
  }, [props.value, value, debounce]);

  return (
    <input
      className="placeholder"
      placeholder={props.placeholder}
      style={styles.textInput}
      value={value}
      onChange={(e) => onChangeValue(e.target.value as any)}
    />
  );
};

const styles = {
  textInput: {
    justifyContent: "center",
    width: "100%",
    fontSize: "16px",
    fontFamily: " Nunito",
    fontWeight: 700,
    backgroundColor: "rgba(255,255,255,0.3)",
    border: "none",
    borderBottom: "1px solid #37bff0",
    cursor: "pointer",
    color: colors.text.light,
    outlineColor: "transparent",
    padding: "15px 10px",
  },
};

export default TextInput;
