import React, { useState, useEffect } from "react";
import _ from "lodash";

import { colors } from "@shared-colors/colors";

type ITextInputProps = {
  value?: string | number;
  placeholder: string;
  rows?: number;
  cols?: number;
  onChange: (value?: any) => void;
};

const TextArea = (props: ITextInputProps) => {
  const debounce = _.debounce(props.onChange, 500);
  const [value, onChangeValue] = useState(props.value ?? "");

  useEffect(() => {
    if (props.value !== value) {
      debounce(value);
    }

    return () => debounce.cancel();
  }, [props.value, value, debounce]);

  return (
    <textarea
      className="placeholder-textarea"
      placeholder={props.placeholder}
      style={styles.textArea}
      value={value}
      rows={props.rows || 1}
      cols={props.cols || undefined}
      onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
        // @ts-ignore
        onChangeValue(e.target.value)
      }
    />
  );
};

const styles = {
  textArea: {
    fontSize: "12px",
    fontFamily: " Nunito",
    fontWeight: 400,
    backgroundColor: colors.background.light,
    border: "none",
    cursor: "pointer",
    color: colors.text.dark,
    outlineColor: colors.accent,
    padding: 10,
  },
};

export default TextArea;
