import React, { useCallback, useEffect, useState, useContext } from "react";
import _ from "lodash";
import Logo from "../components/Logo";
import { useHistory } from "react-router-dom";
import firebase from "firebase/app";
import { useSelector, shallowEqual } from "react-redux";
import "firebase/auth";
import "firebase/database";
import "firebase/firestore";
import Spinner from "react-spinner-material";

import { createCovidScreeningForm } from "@shared/forms/CovidScreening.v1";
import { useAccount } from "@shared/hooks/currentUser.hooks";
import { FormNames, CovidVariants } from "@shared/constants/teams.constants";
import { colors } from "@shared-colors/colors";
import { FormContext } from "@shared/context/Form.context";

import Avatar from "@components/Avatar";
import { AlertContext } from "@components/Alerts";
import Text from "@components/Text";
import View from "@components/View";
import Button from "@components/Button";
import TermConditionModal from "@components/TermConditionModal";
import Archetypes from "../archetypes/index";

const CovidScreeningForm = createCovidScreeningForm(Archetypes);

const Home: React.FC<any> = () => {
  const history = useHistory();

  const currentUser = firebase.auth().currentUser as Teams.CurrentUser;
  const account = useAccount(firebase.firestore(), currentUser.uid);

  const screeningData = useSelector(
    (state: State.AppState) => state.form[FormNames.Screenings] ?? {},
    shallowEqual
  );
  const [lastScreeningDate, setLastScreeningTimestamp] = useState<any>();
  const { dispatch } = useContext(AlertContext);
  const [isInitializing, setIsInitializing] = useState(true);
  const [player, setPlayer] = useState<any>();

  useEffect(() => {
    setIsInitializing(true);
    if (account?.playerId && !lastScreeningDate) {
      const unsubscribe = firebase
        .firestore()
        .collection("Screenings")
        .where("relations.playerId", "==", account?.playerId)
        .where("template", "==", "CovidScreening.v1")
        .onSnapshot((querySnapshot) => {
          let docsData = querySnapshot.docs.map((documentSnapshot) => {
            const screenings = documentSnapshot.data();

            return {
              id: documentSnapshot.id,
              ...screenings,
            } as Teams.CovidScreening;
          });

          docsData = _.sortBy(docsData, ["data.ScreeningDate.timestamp"]);
          if (docsData.length > 0) {
            const lastScreening = docsData[docsData.length - 1];
            if (lastScreening.data?.ScreeningDate?.timestamp) {
              setLastScreeningTimestamp(
                lastScreening.data.ScreeningDate?.timestamp
              );
            }
          }
        });

      setIsInitializing(false);

      return () => unsubscribe();
    }

    setIsInitializing(false);
  }, [account, setLastScreeningTimestamp, lastScreeningDate]);

  useEffect(() => {
    if (account?.playerId && !player) {
      const unsubscribe = firebase
        .firestore()
        .collection("Players")
        .doc(account.playerId)
        .onSnapshot((documentSnapshot) => {
          setPlayer(documentSnapshot.data());
        });

      return () => unsubscribe();
    }
  }, [account, player]);

  const isScreeningDataValid = useCallback(
    (screeningData) => {
      const hasValues =
        screeningData["ScreeningDate"] &&
        screeningData["UnwellCheckCovid"] &&
        screeningData["SymptomsCheckCovid"] &&
        screeningData["TestCheckCovid"] &&
        screeningData["HouseHoldSymptomsCheckCovid"] &&
        screeningData["ContactWithPositiveCheckCovid"];
      if (!hasValues) {
        dispatch({
          type: "addAlert",
          payload: { message: "All fields are required!" },
        });
      }

      return hasValues;
    },
    [dispatch]
  );

  const createCovidScreening = useCallback(
    async (screeningData: any) => {
      try {
        const newDocumentRef = firebase
          .firestore()
          .collection("Screenings")
          .doc();

        if (account && isScreeningDataValid(screeningData)) {
          await newDocumentRef.set({
            data: screeningData,
            template: "CovidScreening.v1",
            summary: {
              lastUpdatedBy: {
                email: account.email,
                uid: account.uid,
              },
            },
            relations: {
              playerId: account.playerId,
            },
          });
          history.push("/submit-confirm");
          firebase.auth().signOut();
        }
      } catch (e) {
        dispatch({
          type: "addAlert",
          payload: {
            message: `Could not save document in collection Screenings. ${e}`,
          },
        });
      }
    },
    [account, dispatch, isScreeningDataValid, history]
  );

  const signout = () => {
    firebase
      .auth()
      .signOut()
      .then(() => {
        history.push("/");
      });
  };

  if (isInitializing || account === undefined || !player) {
    return (
      <View
        className="MQ-landing"
        flexDirection="column"
        style={styles.container}
      >
        <View style={{ justifyContent: "center" }}>
          <Logo maxWidth={300} />
        </View>
        <View flexDirection="column" style={styles.confirmationContainer}>
          <Spinner radius={100} color={colors.secondary} stroke={2} visible />
        </View>
      </View>
    );
  }

  return (
    <View flexDirection="column" style={styles.homepage}>
      <View style={styles.headerBackground}>
        <View
          flexDirection="row"
          style={styles.headerContainer}
          className="MQ-homepage-header"
        >
          <View style={{ alignItems: "center" }}>
            <Avatar
              src={
                player?.data?.Avatar?.image?.uri ??
                require("../assets/avatar.png")
              }
            />
            <View flexDirection="column" style={{ marginLeft: 16 }}>
              <Text
                type="small"
                textTransform="uppercase"
                weight="bold"
                colorVariant="light"
              >
                {player?.data?.Athlete?.Position?.value}
              </Text>
              {account && (
                <Text type="heading1" weight="bold" colorVariant="light">
                  {account?.fullName}
                </Text>
              )}
            </View>
          </View>
          <div
            style={{ alignSelf: "flex-start", cursor: "pointer" }}
            onClick={signout}
          >
            <Text type="heading1" weight="bold" colorVariant="light">
              Logout
            </Text>
          </div>
        </View>
      </View>
      <View
        flexDirection="column"
        style={styles.bodyContainer}
        className="MQ-landing"
      >
        <FormContext.Provider value={FormNames.Screenings}>
          <CovidScreeningForm
            variant={CovidVariants.Player}
            lastScreeningDate={lastScreeningDate}
          />
          <Button
            title="Submit"
            className="button"
            onPress={() => createCovidScreening(screeningData)}
          />
        </FormContext.Provider>
      </View>
      <TermConditionModal isVisible={!account?.accpetedTsAndCs} />
    </View>
  );
};

const styles: any = {
  container: {
    width: "100%",
    height: "70vh",
    justifyContent: "space-between",
    margin: "0 20px",
  },
  confirmationContainer: {
    flex: 1,
    alignItems: "center",
    justifyContent: "center",
    justifyItems: "center",
  },
  homepage: {
    height: "100%",
    width: "100vw",
    backgroundColor: "#EDEDED",
    paddingBottom: 16,
    alignItems: "center",
  },
  bodyContainer: {
    width: "100vw",
    height: "100%",
    marginTop: "128px",
  },
  headerBackground: {
    backgroundColor: colors.primary,
    width: "100vw",
    height: "128px",
    display: "flex",
    justifyContent: "center",
    position: "fixed",
  },
  headerContainer: {
    width: 422,
    margin: "21px",
    alignItems: "center",
    justifyContent: "space-between",
  },
  rowWrapper: {
    marginTop: 8,
    marginBottom: 8,
    padding: 8,
    backgroundColor: "white",
    borderRadius: 10,
  },
};

export default Home;
