import React, { useEffect, useContext, useCallback } from "react";
import { useHistory } from "react-router-dom";
import { AlertContext } from "../components/Alerts";
import firebase from "firebase/app";
import "firebase/auth";
import "firebase/database";
import Spinner from "react-spinner-material";

import { colors } from "@shared-colors/colors";
import View from "@shared-components/View";
import Logo from "@shared-components/Logo";

import { CurrentUserContext } from "../context/CurrentUser.context";

const AccountVerification: React.FC<any> = ({
  notification,
  invitationSource,
}) => {
  const { dispatch } = useContext(AlertContext);
  const history = useHistory();
  const { account } = useContext(CurrentUserContext) as {
    account: Teams.Account;
  };

  const setError = useCallback(
    (error: any) => {
      if (error.message) {
        dispatch({ type: "addAlert", payload: error });
      } else if (typeof error === "string") {
        dispatch({ type: "addAlert", payload: { message: error } });
      } else {
        dispatch({ type: "addAlert", payload: { message: "Unknown error" } });
      }
    },
    [dispatch]
  );

  const redirectBasedOnUrlQuery = useCallback(async () => {
    if (invitationSource === "sms") {
      history.push(`/sms-auth?notificationId=${notification.id}`);
    } else if (invitationSource === "email") {
      history.push(`/email-link-auth?notificationId=${notification.id}`);
    } else {
      throw new Error("initializing/fatal-error");
    }
  }, [history, notification, location, invitationSource]);

  useEffect(() => {
    const initialise = async () => {
      if (notification) {
        if (account) {
          if (notification.receiver?.playerId === account?.playerId) {
            history.push("/home");
          } else {
            firebase.auth().signOut();
            redirectBasedOnUrlQuery();
          }
        }
      }
    };

    try {
      initialise();
    } catch (error) {
      setError(error);
    }
  }, [notification, account, history, setError]);

  return (
    <View
      className="MQ-landing"
      flexDirection="column"
      style={styles.container}
    >
      <View style={{ justifyContent: "center" }}>
        <Logo scale={0.25} />
      </View>

      <View flexDirection="column" style={styles.confirmationContainer}>
        <Spinner radius={100} color={colors.secondary} stroke={2} visible />
      </View>
    </View>
  );
};

const styles: any = {
  container: {
    width: "100%",
    height: "70vh",
    justifyContent: "space-between",
    margin: "0 20px",
  },
  confirmationContainer: {
    flex: 1,
    alignItems: "center",
    justifyContent: "center",
    justifyItems: "center",
  },
  textContainer: {
    marginTop: "60px",
    width: "84%",
    alignSelf: "center",
    justifyContent: "center",
    textAlign: "center",
  },
};

export default AccountVerification;
