import React, { useEffect, useState, useContext, useCallback } from "react";
import { useHistory } from "react-router-dom";
import { AlertContext } from "../components/Alerts";
import AsyncStorage from "@react-native-community/async-storage";
import Modal from "react-modal";
import firebase from "firebase/app";
import "firebase/functions";
import "firebase/auth";
import "firebase/database";
import Spinner from "react-spinner-material";

import { colors } from "@shared-colors/colors";

import View from "../components/View";
import Button from "../components/Button";
import Logo from "../components/Logo";

Modal.setAppElement("#root");

const EmailLinkCheck = () => {
  const url = window.location.href;
  const { dispatch } = useContext(AlertContext);
  const history = useHistory();
  const [emailLinkChecked, setEmailLinkChecked] = useState(false);
  const [notification, setNotification] = useState<any>();

  const setError = useCallback(
    (error: any) => {
      if (error.message) {
        dispatch({ type: "addAlert", payload: error });
      } else if (typeof error === "string") {
        dispatch({ type: "addAlert", payload: { message: error } });
      } else {
        dispatch({ type: "addAlert", payload: { message: "Unknown error" } });
      }
    },
    [dispatch]
  );

  useEffect(() => {
    const getNotificationFromStorage = async () => {
      const notification = await AsyncStorage.getItem(
        "@ScribePro:notification"
      );
      if (notification) {
        const data = JSON.parse(notification);
        setNotification(data);
      } else {
        setNotification(null);
      }
    };
    if (!notification) {
      getNotificationFromStorage();
    }
  }, [setNotification, notification]);

  const setAccountInfo = useCallback(async (notification: any) => {
    const user = firebase.auth().currentUser;
    const { phoneNumber, playerId, fullName } = notification.receiver;
    if (user) {
      const account = await firebase
        .firestore()
        .collection("Accounts")
        .doc(user.uid)
        .get();
      if (account.exists && user) {
        await firebase.firestore().collection("Accounts").doc(user.uid).update({
          type: "USER",
          phoneNumber,
          playerId,
          fullName,
        });
      } else if (user) {
        await firebase.firestore().collection("Accounts").doc(user.uid).set({
          type: "USER",
          phoneNumber,
          playerId,
          fullName,
        });
      }
    } else {
      throw new Error("email-link/missing-user");
    }
  }, []);

  const goToEmailLink = useCallback(async () => {
    if (notification) {
      history.push(`/email-link-auth?notificationId=${notification.id}`);
    } else {
      throw new Error("email-link-check/no-notification-data");
    }
  }, [notification, history]);

  const getEmail = useCallback(async () => {
    if (notification?.receiver.email) {
      return notification?.receiver.email;
    }

    const email = prompt("Please enter your Email", "");

    return email;
  }, [notification]);

  const checkEmailLink = useCallback(async () => {
    try {
      if (notification && !emailLinkChecked) {
        const email = await getEmail();
        if (url && email) {
          const isEmailLinkValid = firebase.auth().isSignInWithEmailLink(url);
          if (isEmailLinkValid) {
            const currentUser = await firebase
              .auth()
              .signInWithEmailLink(email, url);
            await setAccountInfo(notification);
            await AsyncStorage.removeItem("@ScribePro:notification");
            if (currentUser) {
              history.push("/home");
            }
          }
        }
      }
    } catch (error) {
      setError(error);
    } finally {
      setEmailLinkChecked(true);
    }
  }, [
    history,
    url,
    setAccountInfo,
    getEmail,
    notification,
    emailLinkChecked,
    setError,
  ]);

  useEffect(() => {
    try {
      if (notification) {
        if (!emailLinkChecked && url.includes("apiKey") && notification) {
          checkEmailLink();
        }
      }
    } catch (error) {
      setError(error);
    }
  }, [setError, notification, checkEmailLink, emailLinkChecked, url]);

  if (
    notification === undefined ||
    (!emailLinkChecked && url.includes("apiKey"))
  ) {
    return (
      <View
        className="MQ-landing"
        flexDirection="column"
        style={styles.container}
      >
        <View style={{ justifyContent: "center" }}>
          <Logo maxWidth={300} />
        </View>
        <View flexDirection="column" style={styles.confirmationContainer}>
          <Spinner radius={100} color={colors.secondary} stroke={2} visible />
        </View>
      </View>
    );
  }

  return (
    <View
      className="MQ-landing"
      flexDirection="column"
      style={styles.container}
    >
      <View style={{ justifyContent: "center" }}>
        <Logo maxWidth={300} />
      </View>

      <View flexDirection="column">
        <View
          flexDirection="column"
          style={{
            marginBottom: 50,
            width: "100%",
          }}
        />
        <View style={{ width: "100%" }}>
          <Button title="Try again" onPress={() => goToEmailLink()} />
        </View>
      </View>
    </View>
  );
};

const styles: any = {
  container: {
    width: "100%",
    height: "70vh",
    justifyContent: "space-between",
    margin: "0 20px",
  },
  confirmationContainer: {
    flex: 1,
    alignItems: "center",
    justifyContent: "center",
    justifyItems: "center",
  },
  textContainer: {
    marginTop: "60px",
    width: "84%",
    alignSelf: "center",
    justifyContent: "center",
    textAlign: "center",
  },
};

export default EmailLinkCheck;
