import React from "react";
import WebFont from "webfontloader";
import styled from "styled-components";

import { fontSizes, fontWeight } from "@shared/font";
import { colors } from "@shared-colors/colors";

WebFont.load({
  google: {
    families: ["Nunito Web:300,400,700", "sans-serif"],
  },
});

type TextStyleProps = {
  isSelected?: boolean;
  weight?: "bold" | "regular" | "light";
  type?: "heroHead" | "heading1" | "heading2" | "small" | "micro" | "paragraph";
  textAlign?: "center" | "left" | "right";
  colorVariant?:
    | "error"
    | "warning"
    | "success"
    | "primary"
    | "light"
    | "dark"
    | "secondary"
    | "accent";
};

type ITextProps = {
  textTransform?: "none" | "capitalize" | "uppercase";
  style?: any;
} & TextStyleProps;

const StyledText = styled("span")<TextStyleProps>`
  font-weight: ${(props) =>
    props.weight ? fontWeight[props.weight] : "regular"};
  font-family: Nunito;
  color: ${(props) =>
    props.isSelected
      ? colors.text.light
      : props.colorVariant
      ? colors.text[props.colorVariant]
      : colors.text.dark};
  font-size: ${(props) => (props.type ? fontSizes[props.type] : "regular")}px;
  text-align: ${(props) => props.textAlign};
`;

const Text: React.FC<ITextProps> = ({
  children,
  style,
  textTransform,
  ...other
}) => {
  return (
    <StyledText {...other} style={style}>
      {textTransform === "uppercase"
        ? String(children).toUpperCase()
        : children}
    </StyledText>
  );
};

Text.defaultProps = {
  colorVariant: "dark",
  type: "paragraph",
  weight: "regular",
  textAlign: "left",
};

export default Text;
