import _ from "lodash";
import React, { useEffect, useState } from "react";
import moment from "moment";
import { shallowEqual, useDispatch, useSelector } from "react-redux";

import { FormContext } from "@shared/context/Form.context";

import { useValidation } from "@shared/hooks/validation.hooks";
import ErrorsList from "@shared/components/Errors";
import { FormActions } from "@shared/constants/forms.constants";

const serializeValue: Archetypes.serializeToggle = ({
  account,
  created,
  value,
}) => ({
  account,
  created,
  value,
});

export const createToggle: any = ({
  definition,
  valueSelector,
  serialize = serializeValue,
}: {
  definition: Definitions.Toggle;
  valueSelector: any;
  serialize: Archetypes.serializeToggle;
}) => (Component: any) => {
  const Toggle = ({
    path,
    validations,
    layout,
    numberOfColumns,
    filterItems,
    isDisabled,
    svgData,
    isSportTypeLocked,
    account,
  }: any) => {
    const dispatch = useDispatch();

    const formContext = React.useContext(FormContext);
    const [items, setItems] = useState(definition.items);

    const value = useSelector((state: any) => {
      return valueSelector(_.get(state.form, formContext) ?? {}, path);
    }, shallowEqual);

    const contextState = useSelector((state: any) => {
      return _.get(state.form, formContext) ?? {};
    }, shallowEqual);

    const validationResponses = useValidation(value, validations);

    useEffect(() => {
      if (typeof filterItems === "function") {
        const filteredItems = filterItems({
          items: definition.items,
          contextData: contextState,
        });

        if (filterItems && !_.isEqual(items, filteredItems)) {
          if (filteredItems.find((el: any) => el.value === value) === -1) {
            dispatch({
              payload: {
                path,
                formContext,
              },
              type: FormActions.RESET_ENTRY,
            });
          }
          setItems(filteredItems);
        }
      }
    }, [filterItems, contextState]);

    useEffect(() => {
      if (
        isDisabled &&
        isDisabled(contextState) &&
        (value || typeof value === "boolean")
      ) {
        dispatch({
          payload: {
            path,
            formContext,
          },
          type: FormActions.RESET_ENTRY,
        });
      }
    }, [contextState]);

    useEffect(() => {
      if (validationResponses.length >= 1) {
        if (!isDisabled) {
          dispatch({
            type: FormActions.ADD_ENTRY_ERRORS,
            payload: {
              path,
              formContext,
              errors: validationResponses,
            },
          });
        } else if (isDisabled && !isDisabled(contextState)) {
          dispatch({
            type: FormActions.ADD_ENTRY_ERRORS,
            payload: {
              path,
              formContext,
              errors: validationResponses,
            },
          });
        } else {
          dispatch({
            type: FormActions.RESET_ENTRY_ERRORS,
            payload: {
              path,
              formContext,
            },
          });
        }
      }
    }, [validationResponses, contextState]);

    const onChange = (newValue: string | number | null) => {
      let finalValue = newValue;
      if (isValueSelected(newValue)) {
        finalValue = null;
      }

      const newSerialized = serialize({
        created: moment().valueOf(),
        value: finalValue,
        account,
      });

      dispatch({
        payload: {
          path,
          formContext,
          serialized: newSerialized,
        },
        type: FormActions.ADD_ENTRY,
      });
    };

    const isValueSelected = (componentValue: string | number | null) => {
      if (value !== undefined) {
        return value === componentValue;
      }

      return false;
    };

    return (
      <>
        <Component
          {...definition}
          items={items}
          layout={layout}
          numberOfColumns={numberOfColumns}
          value={value}
          isSelected={isValueSelected}
          disabled={isDisabled && isDisabled(contextState)}
          svgData={svgData}
          onChange={onChange}
          isSportTypeLocked={isSportTypeLocked}
        />
        <ErrorsList formContext={formContext} path={path} />
      </>
    );
  };

  return Toggle;
};
