import _ from "lodash";
import React, { useEffect } from "react";
import { shallowEqual, useSelector, useDispatch } from "react-redux";
import moment from "moment";

import { FormContext } from "@shared/context/Form.context";

import ErrorsList from "@shared/components/Errors";
import { FormActions } from "@shared/constants/forms.constants";
import { useValidation } from "@shared//hooks/validation.hooks";

// -----------------------------------------------
const serializeValue: Archetypes.serializeFreeText = ({
  created,
  account,
  value,
}) => ({
  created,
  account,
  text: value,
});

type Props = {
  path: string;
  validations: any;
  isDisabled?: any;
  account: Teams.Account;
};

export const createFreeText: any = ({
  valueSelector,
  serialize = serializeValue,
}: {
  valueSelector: any;
  serialize: Archetypes.serializeFreeText;
}) => (Component: React.FC<any>) => {
  const FreeTextArchetype = ({
    path,
    validations,
    isDisabled,
    account,
    ...other
  }: Props) => {
    const dispatch = useDispatch();
    const formContext = React.useContext(FormContext);

    const value = useSelector((state: State.AppState) => {
      return valueSelector(_.get(state.form, formContext) ?? {}, path);
    }, shallowEqual);

    const contextState = useSelector((state: any) => {
      return _.get(state.form, formContext) ?? {};
    }, shallowEqual);

    const validationResponses = useValidation(value, validations);

    useEffect(() => {
      if (isDisabled && isDisabled(contextState) && value) {
        dispatch({
          payload: {
            path,
            formContext,
          },
          type: FormActions.RESET_ENTRY,
        });
      }
    }, [contextState]);

    useEffect(() => {
      if (validationResponses.length >= 1) {
        dispatch({
          type: FormActions.ADD_ENTRY_ERRORS,
          payload: {
            path,
            formContext,
            errors: validationResponses,
          },
        });
      }
    }, [validationResponses]);

    const onChange = (newValue: string) => {
      const newSerialized = serialize({
        created: moment().valueOf(),
        account,
        value: newValue,
      });

      dispatch({
        payload: {
          path,
          formContext,
          serialized: newSerialized,
        },
        type: FormActions.ADD_ENTRY,
      });
    };

    return (
      <>
        <Component
          {...other}
          onChangeText={onChange}
          value={value}
          disabled={isDisabled && isDisabled(contextState)}
        />
        <ErrorsList formContext={formContext} path={path} />
      </>
    );
  };

  return FreeTextArchetype;
};
