import React, { useEffect, useState, useContext, useCallback } from "react";
import Spinner from "react-spinner-material";
import { useHistory, useLocation } from "react-router-dom";
import queryString from "query-string";
import firebase from "firebase/app";
import "firebase/auth";
import "firebase/database";

import { colors } from "@shared-colors/colors";

import Text from "../components/Text";
import { AlertContext } from "../components/Alerts";
import Button from "../components/Button";
import View from "../components/View";
import TextInput from "../components/TextInput";
import Logo from "../components/Logo";
import { useNotification } from "../hooks/notifications.hooks";

declare global {
  interface Window {
    recaptchaVerifier: any;
  }
}

const isAuthenticated = () => {
  return firebase.auth().currentUser !== null;
};

const SmsAuth = () => {
  const history = useHistory();
  const location = useLocation();
  const { dispatch } = useContext(AlertContext);
  const [isLoading, setIsLoading] = useState(false);

  const [confirmationResult, setConfirmationResult] = useState<any>();
  const [verificationCode, setVerificationCode] = useState<string>("");

  const { notificationId } = queryString.parse(location.search);
  const notification = useNotification(notificationId as string);

  const setError = useCallback(
    (error: any) => {
      if (error.message) {
        dispatch({ type: "addAlert", payload: error });
      } else if (typeof error === "string") {
        dispatch({ type: "addAlert", payload: { message: error } });
      } else {
        dispatch({ type: "addAlert", payload: { message: "Unknown error" } });
      }
    },
    [dispatch]
  );

  useEffect(() => {
    window.recaptchaVerifier = new firebase.auth.RecaptchaVerifier(
      "recaptchaVerifier",
      {
        size: "invisible",
      }
    );
    if (isAuthenticated()) {
      history.push("/home");
    }
  }, [history]);

  const sendVerificationCode = async () => {
    setIsLoading(true);
    try {
      const { prefix, value } = notification.receiver.phoneNumber;
      const appVerifier = window.recaptchaVerifier;
      const confirmation = await firebase
        .auth()
        .signInWithPhoneNumber(`${prefix}${value}`, appVerifier);
      setConfirmationResult(confirmation);
    } catch (error) {
      setError(error);
    } finally {
      setIsLoading(false);
    }
  };

  const setAccountInfo = useCallback(
    async (user: any) => {
      const { receiver } = notification;
      const account = await firebase
        .firestore()
        .collection("Accounts")
        .doc(user.uid)
        .get();
      if (account.exists && user) {
        await firebase.firestore().collection("Accounts").doc(user.uid).update({
          type: "USER",
          phoneNumber: receiver.phoneNumber,
          playerId: receiver.playerId,
          fullName: receiver.fullName,
        });
      } else if (user) {
        await firebase.firestore().collection("Accounts").doc(user.uid).set({
          type: "USER",
          phoneNumber: receiver.phoneNumber,
          playerId: receiver.playerId,
          fullName: receiver.fullName,
        });
      } else {
        throw Error("Something went wrong.");
      }
    },
    [notification]
  );

  const verifyVerificationCode = async (code: string) => {
    try {
      setIsLoading(true);
      const result = await confirmationResult.confirm(code);
      const user = result.user;
      await setAccountInfo(user);
      history.push("/home");
    } catch (error) {
      setError(error);
    } finally {
      setIsLoading(false);
    }
  };

  if (notification === undefined) {
    return (
      <View
        className="MQ-landing"
        flexDirection="column"
        style={styles.container}
      >
        <View style={{ justifyContent: "center" }}>
          <Logo maxWidth={300} />
        </View>
        <View flexDirection="column" style={styles.confirmationContainer}>
          <Spinner radius={100} color={colors.secondary} stroke={2} visible />
        </View>
      </View>
    );
  }

  return (
    <View
      className="MQ-landing"
      flexDirection="column"
      style={styles.container}
    >
      <View style={{ justifyContent: "center" }}>
        <Logo maxWidth={300} />
      </View>
      {confirmationResult ? (
        <View flexDirection="column">
          <View style={{ marginBottom: 50, width: "100%" }}>
            <TextInput
              key="sms_code"
              value={verificationCode}
              placeholder="Sms Code"
              onChange={(value) => setVerificationCode(value as string)}
            />
          </View>
          <View style={{ width: "100%" }}>
            <Button
              isLoading={isLoading}
              title="Verify SMS Code"
              onPress={() => verifyVerificationCode(verificationCode)}
            />
          </View>
        </View>
      ) : (
        <View flexDirection="column">
          <View
            flexDirection="column"
            style={{
              marginBottom: 50,
              width: "100%",
            }}
          >
            <Text
              colorVariant="light"
              type="heading1"
              weight="bold"
              style={{ marginBottom: 16 }}
            >
              Your clinician has invited you to track your covid symptoms.
            </Text>
            <Text colorVariant="light" type="heading1" weight="bold">
              {`We'll send a confirmation code to this number ${notification?.receiver?.phoneNumber?.prefix}${notification?.receiver?.phoneNumber?.value} if your number is not correct contact your clinician.`}
            </Text>
          </View>
          <View style={{ width: "100%" }}>
            <Button
              isLoading={isLoading}
              title="Send SMS code"
              onPress={() => sendVerificationCode()}
            />
          </View>
        </View>
      )}
    </View>
  );
};

const styles: any = {
  container: {
    width: "100%",
    height: "70vh",
    justifyContent: "space-between",
    margin: "0 20px",
  },
  confirmationContainer: {
    flex: 1,
    alignItems: "center",
    justifyContent: "center",
    justifyItems: "center",
  },
  textContainer: {
    marginTop: "60px",
    width: "84%",
    alignSelf: "center",
    justifyContent: "center",
    textAlign: "center",
  },
};

export default SmsAuth;
