import React from "react";

import Icon from "@shared-components/Icon";
import Text from "@shared-components/Text";
import View from "@shared-components/View";

type Props = {
  iconName?: string;
  title: string;
  required?: boolean;
};

const InputLabel: React.FC<Props> = ({ iconName, title, required }) => {
  return (
    <View style={styles.container as React.CSSProperties | any}>
      {iconName && <Icon name={iconName} size={14} />}
      <Text type="small" textTransform="uppercase" style={styles.textStyles}>
        {title}
      </Text>
      {required && (
        <Text type="small" style={styles.asterix}>
          *
        </Text>
      )}
    </View>
  );
};

const styles = {
  container: {
    flexDirection: "row",
    paddingBottom: 8,
    marginBottom: 0,
  },
  textStyles: {
    marginBottom: 2,
  },
  asterix: {
    marginLeft: 4,
  },
};

export default InputLabel;
