import React from "react";

import Text from "@shared-components/Text";
import View from "@shared-components/View";

type ErrorProps = {
  error: any;
  key: number;
  highlight?: boolean;
};

const ErrorItem: React.FC<ErrorProps> = (props) => {
  if (props.error && props.error) {
    return (
      <View style={styles.error as any}>
        {props.highlight && (
          <Text
            style={[styles.errorText, props.highlight && styles.highlighted]}
            weight="bold"
          >
            {props.error}
          </Text>
        )}
      </View>
    );
  }

  return null;
};

const styles = {
  error: {
    display: "flex",
    paddingLeft: 4,
    paddingRight: 4,
  },
  errorText: {
    fontSize: 8,
  },
  highlighted: {
    color: "red",
  },
};

export default ErrorItem;
