import React from "react";

type Props = {
  isSelected?: boolean;
  item: any;
  layout: "list-horizontal" | "list-vertical" | "grid";
  isFirst?: boolean;
  isLast?: boolean;
  width?: number;
  onPress: () => void;
  disabled?: boolean;
};

const Pressable: React.FC<Props> = ({ children, onPress, ...other }) => {
  return (
    <button onClick={onPress} {...other} style={{ border: "none" }}>
      {children}
    </button>
  );
};

export default Pressable;
