import React from "react";

type IViewProps = {
  flexDirection?: "row" | "column";
  children?: JSX.Element[] | JSX.Element | any;
  style?: React.CSSProperties;
  className?: string;
};

function View(props: IViewProps) {
  const flexDirection =
    props.flexDirection === "column" ? styles.column : styles.row;
  const style = {
    display: "flex",
    ...flexDirection,
    ...props.style,
  };

  return (
    <div style={style} className={props.className}>
      {props.children}
    </div>
  );
}

const styles: any = {
  column: {
    flexDirection: "column",
  },
  row: {
    flexDirection: "row",
  },
};

export default View;
