import React, { useReducer, useContext } from "react";

import { colors } from "@shared-colors/colors";

import Text from "@shared-components/Text";
import View from "@shared-components/View";
import Button from "@shared-components/Button";

type AlertProps = {
  message?: string;
};

/* USEREDUCER */
const initialState = {};

function alertReducer(state: typeof initialState, action: any) {
  switch (action.type) {
    case "removeAlert":
      return {};
    case "addAlert":
      return {
        message: action.payload.message,
      };
    default:
      return state;
  }
}

export const AlertContext = React.createContext<any>({
  state: {},
  dispatch: null,
});

export const AlertContextWrapper = (props: any) => {
  const [state, dispatch] = useReducer(alertReducer, initialState);

  return (
    <AlertContext.Provider value={{ state, dispatch }}>
      <Alerts />
      {props.children}
    </AlertContext.Provider>
  );
};

const Alerts = (_props: AlertProps) => {
  const { state, dispatch } = useContext(AlertContext);

  return (
    <View>
      {state && state.message && (
        <View className="alerts" style={styles.container}>
          <Text colorVariant="light" weight="bold" type="small">
            {state.message}
          </Text>
          <Button
            className="btn-alert"
            title="x"
            onPress={() => dispatch({ type: "removeAlert" })}
          />
        </View>
      )}
    </View>
  );
};

const styles = {
  container: {
    display: "grid",
    gridTemplateColumns: "1fr 30px",
    alignItems: "center",
    maxWidth: "100%",
    height: "auto",
    backgroundColor: colors.error,
    position: "fixed" as const,
    bottom: 0,
    margin: 10,
    borderRadius: 8,
    padding: 10,
    boxShadow: "rgba(0, 0, 0, 0.4) 3px 6px 9px",
  },
};

export default Alerts;
