import React from "react";
import { useSelector, shallowEqual } from "react-redux";

import View from "@shared-components/View";
import ErrorItem from "./ErrorItem";

type Props = {
  formContext: string;
  path: string;
};

const ErrorsList: React.FC<Props> = ({ formContext, path }) => {
  const { showArchetypeErrors, archetypes } = useSelector(
    (state: State.AppState) => {
      return (state.errors?.[formContext] ?? {}) as any;
    },
    shallowEqual
  );

  const errors =
    archetypes && archetypes[path]
      ? archetypes[path].errors.filter((error: any) => !error.valid)
      : [];

  const renderErrorItem = (
    item: Archetypes.ValidationResult,
    index: number
  ) => (
    <ErrorItem
      error={item.message}
      key={index}
      highlight={showArchetypeErrors}
    />
  );

  return (
    <View style={styles.errorsContainer as any}>
      {errors && errors.map(renderErrorItem)}
    </View>
  );
};

const styles = {
  errorsContainer: {
    paddingTop: 4,
    width: "100%",
    display: "flex",
    flexDirection: "row",
    minHeight: 16,
  },
};

export default ErrorsList;
