import _ from "lodash";
import moment from "moment";

export const timestampValueSelector = <
  D extends {
    [index: string]: unknown;
  },
  A extends string
>(
  data: D,
  path: A
) => {
  const archetype = _.get(data, path) as Archetypes.Timestamp;

  return archetype ? archetype.timestamp : undefined;
};

export const timestampDisplayValueSelector = <D, A extends string>(
  data: D,
  path: A
) => {
  const archetype = _.get(data, path) as Archetypes.Timestamp;

  return archetype
    ? moment(archetype.timestamp).format("DD/MM/yyyy")
    : undefined;
};

