export const phoneValueSelector = <D, A extends string>(data: D, path: A) => {
  const archetype = _.get(data, path) as Archetypes.PhoneNumber;

  if (archetype && typeof archetype.phoneNumber === "object") {
    const { phoneNumber } = archetype;

    return phoneNumber;
  }

  return archetype ? archetype.phoneNumber : undefined;
};

