export const toggleViewSelector = <D, A extends string>(
  data: D,
  path: A,
  definition: Definitions.Toggle
) => {
  const archetype = _.get(data, path) as Archetypes.Toggle;

  const item = _.find(
    definition.items,
    (o: any) => o.value === archetype?.value
  );

  return item?.display;
};

