export const painScoreValueSelector = <D, A extends string>(
  data: D,
  path: A
) => {
  const archetype = _.get(data, path) as Archetypes.PainScore;

  return typeof archetype === "object" && archetype.score !== undefined
    ? archetype.score
    : undefined;
};

