export const multiSelectValuesSelector = <D, A extends string>(
  data: D,
  path: A
) => {
  const archetype = _.get(data, path) as Archetypes.MultiSelect;

  return archetype ? archetype.values : undefined;
};

export const multiSelectViewSelector = <D, A extends string>(
  data: D,
  path: A,
  definition: Definitions.MultiSelect
) => {
  const archetype = _.get(data, path) as Archetypes.MultiSelect;

  const items = definition.items.filter((o) =>
    archetype?.values?.includes(o.value)
  );

  if (_.isEmpty(items) || typeof items === "number") {
    return undefined;
  }

  return items.map((item) => item.display).join(" | ");
};

