import React, { useState } from "react";
import moment, { Moment } from "moment";
import RDayPicker from "react-day-picker";
import "react-day-picker/lib/style.css";

import View from "./View";
import Text from "./Text";
import Modal from "./Modal";
import Button from "./Button";
import Box from "./Box";

type DayPickerProps = {
  onChange: (moment: Moment) => void;
  modalTitle: string;
  value: Moment;
  title: string;
  subtitle?: string;
  isSelected: boolean;
};

const DatePicker: React.FC<DayPickerProps> = (props) => {
  const [newValue, setNewValue] = useState<Moment>(
    props.value ? moment(props.value) : moment()
  );

  const renderComponent = (closeModal: () => void) => {
    const onConfirm = () => {
      props.onChange(newValue ?? moment());
      closeModal();
    };

    return (
      <View style={{ flex: 1 }} flexDirection="column">
        <View style={{ flex: 9 }}>
          <RDayPicker
            selectedDays={newValue && newValue.toDate()}
            onDayClick={(day: Date) => setNewValue(moment(day))}
          />
        </View>
        <Button colorVariant="primary" title="confirm" onPress={onConfirm} />
      </View>
    );
  };

  return (
    <Modal
      presentationStyle="formSheet"
      headerTitleComponent={<Text type="heading1">{props.modalTitle}</Text>}
      renderModalContentComponent={renderComponent}
    >
      <Box title={props.title} isSelected={props.isSelected} />
    </Modal>
  );
};

DatePicker.defaultProps = {
  modalTitle: "Datepicker",
};

export default DatePicker;
