import React, { useEffect, useState, useContext, useCallback } from "react";
import AsyncStorage from "@react-native-community/async-storage";
import Spinner from "react-spinner-material";

import firebase from "firebase/app";
import "firebase/auth";
import "firebase/database";

import { colors } from "@shared-colors/colors";

import Text from "../components/Text";
import { AlertContext } from "@shared-components/Alerts";
import View from "@shared-components/View";
import Button from "@shared-components/Button";
import Logo from "@shared-components/Logo";

// Modal.setAppElement("#root");

const EmailLinkAuth: React.FC<any> = () => {
  const { dispatch } = useContext(AlertContext);
  const [confirmationResult, setConfirmationResult] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [notification, setNotification] = useState<any>();

  useEffect(() => {
    const getNotificationFromStorage = async () => {
      const notification = await AsyncStorage.getItem(
        "@ScribePro:notification"
      );
      if (notification) {
        const data = JSON.parse(notification);
        setNotification(data);
      }
    };
    if (!notification) {
      getNotificationFromStorage();
    }
  }, [setNotification, notification]);

  const setError = useCallback(
    (error: any) => {
      if (error.message) {
        dispatch({ type: "addAlert", payload: error });
      } else if (typeof error === "string") {
        dispatch({ type: "addAlert", payload: { message: error } });
      } else {
        dispatch({ type: "addAlert", payload: { message: "Unknown error" } });
      }
    },
    [dispatch]
  );

  const sendEmailLink = useCallback(async () => {
    try {
      setIsLoading(true);
      if (notification?.receiver?.email) {
        await firebase.functions().httpsCallable("sendEmailLinkCovidDashboard")(
          {
            userEmail: notification.receiver.email,
          }
        );

        setConfirmationResult(true);
      } else {
        throw Error("email-link/missing-email");
      }
    } catch (error) {
      setError(error);
    } finally {
      setIsLoading(false);
    }
  }, [notification, setError]);

  if (notification === undefined) {
    return (
      <View
        className="MQ-landing"
        flexDirection="column"
        style={styles.container}
      >
        <View style={{ justifyContent: "center" }}>
          <Logo scale={0.25} />
        </View>
        <View flexDirection="column" style={styles.confirmationContainer}>
          <Spinner radius={100} color={colors.secondary} stroke={2} visible />
        </View>
      </View>
    );
  }

  return (
    <View
      className="MQ-landing"
      flexDirection="column"
      style={styles.container}
    >
      <View style={{ justifyContent: "center" }}>
        <Logo scale={0.25} />
      </View>

      {confirmationResult ? (
        <View flexDirection="column" style={styles.confirmationContainer}>
          <View style={styles.textContainer}>
            <Text colorVariant="light" type="heading1" weight="bold">
              We&apos;ve just seent you an email with an email link to log in in
              ScribePro. Click the link on your phone to access your account.
              Check junk or spam and if nothing has arrived log in with sms.
            </Text>
          </View>
        </View>
      ) : (
        <View flexDirection="column">
          <View
            flexDirection="column"
            style={{
              marginBottom: 50,
              width: "100%",
            }}
          >
            <Text
              colorVariant="light"
              type="heading1"
              weight="bold"
              style={{ marginBottom: 16 }}
            >
              Your clinician has invited you to track your covid symptoms.
            </Text>
            <Text colorVariant="light" type="heading1" weight="bold">
              {`We'll send an email link to this email ${notification?.receiver?.email} if your email is not correct contact your clinician.`}
            </Text>
          </View>
          <View style={{ width: "100%" }}>
            <Button
              isLoading={isLoading}
              title="Send email link"
              onPress={() => sendEmailLink()}
            />
          </View>
        </View>
      )}
    </View>
  );
};

const styles: any = {
  container: {
    width: "100%",
    height: "70vh",
    justifyContent: "space-between",
    margin: "0 20px",
  },
  confirmationContainer: {
    flex: 1,
    alignItems: "center",
    justifyContent: "center",
    justifyItems: "center",
  },
  textContainer: {
    marginTop: "60px",
    width: "84%",
    alignSelf: "center",
    justifyContent: "center",
    textAlign: "center",
  },
};

export default EmailLinkAuth;
