import React, { useContext } from "react";

import { CurrentUserContext } from "../context/CurrentUser.context";

import { createToggle } from "@shared/archetypesCreators/createToggle";
import { createTimestamp } from "@shared/archetypesCreators/createTimestamp";
import { createFreeText } from "@shared/archetypesCreators/createFreeText";

import {
  textValueSelector,
  toggleValueSelector,
  timestampValueSelector,
  timestampDisplayValueSelector,
} from "@shared/selectors";

import definitions from "@shared/definitions/index";

import ButtonGroup from "@shared/components/ButtonGroup";
import TextArea from "@shared-components/TextArea";
import DatePicker from "@shared-components/DatePicker";
import TextInput from "@shared-components/TextInput";

const withAccount = (Archetype: any) => {
  const ArchetypeWithAccount = (props: any) => {
    const { account } = useContext(CurrentUserContext) as {
      account: Teams.Account;
    };

    return <Archetype {...props} account={account} />;
  };

  return ArchetypeWithAccount;
};

export default {
  // -----------------------------------------------
  // ----------------NEW----------------------------
  // -----------------------------------------------

  Text: withAccount(
    createFreeText({
      valueSelector: textValueSelector,
    })(TextInput)
  ),

  CovidConfirmation: withAccount(
    createToggle({
      definition: definitions.covidConfirmation,
      valueSelector: toggleValueSelector,
    })(ButtonGroup)
  ),

  Notes: withAccount(
    createFreeText({
      valueSelector: textValueSelector,
    })(TextArea)
  ),

  Timestamp: withAccount(
    createTimestamp({
      valueSelector: timestampValueSelector,
      valueDisplaySelector: timestampDisplayValueSelector,
    })(DatePicker)
  ),

  YesOrNo: withAccount(
    createToggle({
      definition: definitions.yesOrNo,
      valueSelector: toggleValueSelector,
    })(ButtonGroup)
  ),
};
