import React from "react";

import firebase from "firebase";
import { useAccount } from "@shared/hooks/currentUser.hooks";

export const CurrentUserContext = React.createContext<{
  currentUser: Teams.CurrentUser | null | undefined;
  account: Teams.Account | null | undefined;
}>({ account: null, currentUser: null });

const CurrentUserContextProvider: React.FC<{
  currentUser: Teams.CurrentUser;
}> = ({ children, currentUser }) => {
  const account = useAccount(firebase.firestore(), currentUser.uid);

  if (account === undefined) {
    return <div />;
  }

  return (
    <CurrentUserContext.Provider
      value={{
        currentUser: currentUser as Teams.CurrentUser,
        account: account as Teams.Account,
      }}
    >
      {children}
    </CurrentUserContext.Provider>
  );
};

export default CurrentUserContextProvider;
