import _ from "lodash";

import { FormActions, FormStatus } from "@shared/constants/forms.constants";

const initialState = {};

const errorsReducer = (
  state: State.ErrorState = initialState,
  action: State.Actions
): State.ErrorState => {
  switch (action.type) {
    case FormActions.SHOW_NESTED_FORM_ERROR: {
      const stateClone: State.ErrorState = _.cloneDeep(state);

      for (const formInContext of Object.values(stateClone)) {
        if (formInContext.formStatus === FormStatus.EMPTY) {
          formInContext.formStatus = FormStatus.HIGHLIGHTED as any;
        }
      }

      return stateClone;
    }
    case FormActions.SET_COMPLETE_NESTED_FORM: {
      const { formContext } = action.payload;

      return {
        ...state,
        [formContext]: {
          ...state[formContext],
          formStatus: FormStatus.COMPLETE,
        },
      };
    }
    case FormActions.SET_EMPTY_NESTED_FORM: {
      const { formContext } = action.payload;

      return {
        ...state,
        [formContext]: {
          ...state[formContext],
          formStatus: FormStatus.EMPTY,
        },
      };
    }
    case FormActions.ADD_ENTRY_ERRORS: {
      const { formContext, path, errors } = action.payload;
      const formContextArchetypes = state[formContext]?.archetypes ?? {};

      return {
        ...state,
        [formContext]: {
          ...state[formContext],
          showArchetypeErrors: false,
          archetypes: {
            ...formContextArchetypes,
            [path]: {
              ...formContextArchetypes[path],
              errors,
            },
          },
        },
      };
    }
    case FormActions.SHOW_ENTRIES_ERRORS: {
      const { formContext } = action.payload;
      const stateClone = _.cloneDeep(state);

      const nestedFormsNames = Object.keys(stateClone).filter(
        (formName) => formName.includes(formContext) && formName !== formContext
      );

      for (const formName of nestedFormsNames) {
        const formInContext = stateClone[formName];

        if (formInContext.formStatus === FormStatus.EMPTY) {
          formInContext.formStatus = FormStatus.HIGHLIGHTED;
        }
      }

      return {
        ...stateClone,
        [formContext]: {
          ...stateClone[formContext],
          showArchetypeErrors: true,
        },
      };
    }
    case FormActions.RESET_FORM: {
      const { formContext } = action.payload;
      const stateClone = _.cloneDeep(state);

      const nestedFormsNames = Object.keys(stateClone).filter((formName) =>
        formName.includes(formContext)
      );

      for (const formName of nestedFormsNames) {
        _.unset(stateClone, formName);
      }

      return stateClone;
    }
    case FormActions.RESET_ENTRY_ERRORS: {
      const { formContext, path } = action.payload;
      const stateClone = _.cloneDeep(state);
      const archetypesErrors = stateClone[formContext];

      _.unset(archetypesErrors, `archetypes.${path}`);

      return stateClone;
    }
    case FormActions.RESTORE_CACHED_FORM_DATA: {
      const { formContext } = action.payload;
      const stateClone = _.cloneDeep(state);
      delete stateClone[formContext];

      return stateClone;
    }
    default:
      return state;
  }
};

export default errorsReducer;
