import React, { useState, useEffect } from "react";
import _ from "lodash";

import { colors } from "@shared-colors/colors";

type Props = {
  value?: string;
  placeholder: string;
  onChangeText: (_value?: string) => void;
  style?: React.CSSProperties;
};

const TextInput: React.FC<Props> = ({
  style = {},
  onChangeText,
  value,
  placeholder,
}) => {
  const debounce = _.debounce(onChangeText, 500);
  const [text, onChangeValue] = useState(value ?? "");

  useEffect(() => {
    if (value !== text) {
      debounce(text);
    }

    return () => debounce.cancel();
  }, [value, text, debounce]);

  const setText = (event: any) => {
    onChangeValue(event.target.value);
  };

  return (
    <input
      className="placeholder"
      placeholder={placeholder}
      style={{ ...styles.textInput, ...style }}
      value={value}
      onChange={setText}
    />
  );
};

const styles = {
  textInput: {
    color: colors.text.dark,
    fontFamily: "Nunito",
    width: "100%",
    paddingLeft: 8,
    fontSize: 16,
    backgroundColor: "#fff",
    borderRadius: 5,
    border: "none",

    margin: "15px 0",
    padding: "15px 10px",
  },
  disabled: {
    opacity: 0.5,
  },
  lightBackground: {
    backgroundColor: "#fff",
  },
};

export default TextInput;
