import _ from "lodash";
import { useEffect, useState } from "react";

export const useValidation = (
  value: string,
  validators: {
    [key: string]: (value: string) => Archetypes.ValidationResult;
  }
) => {
  const [validityResponses, setValidityResponses] = useState<
    Archetypes.ValidationResult[] | []
  >([]);

  useEffect(() => {
    const responses = [];
    if (validators) {
      for (const validator of Object.values(validators)) {
        const response = validator(value);
        responses.push(response);
      }
      setValidityResponses(responses);
    }
  }, [value, validators]);

  return validityResponses;
};

export const checkValidInputPhoneNumber = (
  phoneNumber: Teams.PhoneNumber
): Archetypes.ValidationResult => {
  const result = {
    valid: false,
    message: "Field is required",
    name: "inputRequired",
  };

  if (
    typeof phoneNumber === "object" &&
    !_.isEmpty(phoneNumber.value) &&
    !_.isEmpty(phoneNumber.prefix) &&
    !_.isEmpty(phoneNumber.countryCode)
  ) {
    result.valid = true;
  }

  return result;
};

export const checkValidInput = (value: string): Archetypes.ValidationResult => {
  const result = {
    valid: false,
    message: "Field is required",
    name: "inputRequired",
  };
  if (typeof value !== "undefined") {
    result.valid = true;
  }

  return result;
};

export const checkValidEmail = (value: string): Archetypes.ValidationResult => {
  const re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
  const isValid = re.test(String(value).toLowerCase());

  const result = {
    valid: false,
    message: "Email is not valid",
    name: "emailNotValid",
  };
  if (isValid) {
    result.valid = true;
  }

  return result;
};

export const checkMinLength = (minLength: number) => (
  value: string
): Archetypes.ValidationResult => {
  const result = {
    valid: false,
    message: `Field needs to be at least ${minLength} characters long`,
    name: "minLength",
  };
  if (value?.length >= minLength) {
    result.valid = true;
  }

  return result;
};

export const telephoneCheck = (value?: string) => {
  const result = {
    valid: false,
    message: "Phone number is not valid",
    name: "phoneNumberNotValid",
  };
  if (!value) {
    return result;
  }

  const phoneReg = /^(\+)(\s*\d){7,14}$/;

  if (`${value}`.match(phoneReg)) {
    result.valid = true;
  }

  return result;
};

export const booleanCheck = (value?: boolean) => {
  const result = {
    valid: false,
    message: "Field is required",
    name: "booleanRequired",
  };
  if (value !== null && value !== undefined) {
    result.valid = true;
  }

  return result;
};
