import React from "react";

import "../fontello/css/fontello.css";

type IIconProps = {
  name: string;
  color?: string;
  size?: number | any;
};

function Icon(props: IIconProps) {
  const color = props.color ?? "black";
  const size = props.size ?? 24;

  return (
    <i className={`icon-${props.name}`} style={{ fontSize: size, color }} />
  );
}

export default Icon;
