import React, { useState, useEffect } from "react";
import styled from "styled-components";

import Item from "./Item";
import View from "@shared-components/View";

type ItemValue = {
  value: string;
  display: string;
  text?: string;
};

type Props = {
  items: Array<ItemValue>;
  onChange: (value?: string) => void;
  isSelected: (buttonValue: string) => boolean;
  numberOfColumns?: number;
  text?: string;
  disabled?: boolean;
  isSportTypeLocked?: boolean;
} & StyleProps;

type StyleProps = {
  layout?: "list-horizontal" | "list-vertical" | "grid";
};

const StyledView = styled(View)<StyleProps>`
  flex: 1;
  flex-wrap: ${(props) => (props.layout === "grid" ? "wrap" : "nowrap")};
  flex-direction: ${(props) =>
    props.layout !== "list-vertical" ? "row" : "column"};
`;

const ButtonGroup: React.FC<Props> = ({
  items,
  numberOfColumns = 3,
  layout = "list-horizontal",
  isSelected,
  onChange,
  disabled,
  isSportTypeLocked,
}) => {
  const [matrixItems, setMatrixItems] = useState<any>([]);

  useEffect(() => {
    const matrix: any = [];
    if (layout === "grid") {
      let tempArray: any = [];

      for (const item of items) {
        if (tempArray.length === numberOfColumns) {
          matrix.push(tempArray);
          tempArray = [];
        }
        tempArray.push(item);
      }

      if (tempArray.length > 0) {
        matrix.push(tempArray);
      }

      setMatrixItems(matrix);
    }
  }, [items]);

  const renderItem = (item: ItemValue, index: number) => {
    const isDisabled = disabled || isSportTypeLocked;

    return (
      <Item
        key={index}
        item={item}
        itemsNum={items.length}
        index={index}
        layout={layout}
        disabled={isDisabled}
        isSelected={isSelected(item.value)}
        onPress={onChange}
      />
    );
  };

  if (layout === "grid") {
    return (
      <StyledView layout={"list-vertical"}>
        {matrixItems.map((items: any, index: any) => (
          <View key={index} style={{ flexDirection: "row" }}>
            {items.map(renderItem)}
          </View>
        ))}
      </StyledView>
    );
  }

  return <StyledView layout={layout}>{items.map(renderItem)}</StyledView>;
};

export default ButtonGroup;
