import React from "react";
import "react-day-picker/lib/style.css";

import Text from "./Text";
import View from "./View";

type IBoxProps = {
  onPress?: () => void;
  title: string;
  isSelected: boolean;
};

function Box(props: IBoxProps) {
  return (
    <div style={{ flex: 1 }} onClick={props.onPress}>
      <View
        style={{ backgroundColor: "#EDEDED", padding: 8, cursor: "pointer" }}
      >
        <Text type="paragraph">{props.title}</Text>
      </View>
    </div>
  );
}

export default Box;
