import _ from "lodash";
import { useEffect, useState } from "react";

export const useCurrentUser = (firebaseAuth: any): Teams.CurrentUser | null => {
  const [currentUser, setCurrentUser] = useState<Teams.CurrentUser | null>(
    firebaseAuth.currentUser?.toJSON() as Teams.CurrentUser
  );

  useEffect(() => {
    let mounted = true;
    firebaseAuth.onAuthStateChanged((user: Teams.CurrentUser | null) => {
      if (mounted) {
        if (user && !currentUser) {
          setCurrentUser(user as Teams.CurrentUser);
        } else if (!user && currentUser) {
          setCurrentUser(null);
        }
      }
    });

    return () => {
      mounted = false;
    };
  }, [currentUser]);

  return currentUser;
};

export const useAccount = (
  firestore: any,
  accountId: string
): Teams.Account | null | undefined => {
  const [account, setAccount] = useState<Teams.Account | null>();

  useEffect(() => {
    const unsubscribe = firestore
      .collection("Accounts")
      .doc(accountId)
      .onSnapshot(
        (documentSnapshot: { data: () => Teams.Account }) => {
          const data = documentSnapshot.data();
          if (data) {
            const accountData = {
              uid: accountId,
              fullName: data?.fullName,
              firstname: data?.firstname,
              lastname: data?.lastname,
              playerId: data?.playerId,
              selectedTeamId: data?.selectedTeamId,
              email: data.email,
              phoneNumber: data.phoneNumber,
              accpetedTsAndCs: data.accpetedTsAndCs,
              appVersion: "1.2.1",
            };

            if (!_.isEqual(account, accountData)) {
              setAccount(accountData);
            }
          } else {
            setAccount(null);
          }
        },
        (error: any) => {
          throw error;
        }
      );

    return () => unsubscribe();
  }, [account, accountId]);

  return account;
};
