import React from "react";
import moment from "moment";

import Text from "@shared-components/Text";
import View from "@shared-components/View";
import Hyperlink from "@shared-components/Hyperlink";
import FormInputContainer from "@shared-components/FormInputContainer";
import { CovidPaths, CovidVariants } from "@shared/constants/teams.constants";

import { checkValidInput } from "@shared/hooks/validation.hooks";

import { toggleValueSelector } from "@shared/selectors";
import InputLabel from "@shared-components/InputLabel";

type Props = {
  lastScreeningDate?: Date;
  variant: Teams.CovidVariants;
};

const createCovidScreeningForm = (Archetypes: {
  [archetype: string]: React.FC<any>;
}) => ({ lastScreeningDate, variant }: Props) => {
  const moment10daysAgo = moment().subtract(10, "d");
  const moment14daysAgo = moment().subtract(14, "d");

  const lastScreeningDateMoment = lastScreeningDate
    ? moment(lastScreeningDate)
    : moment().days(14);

  const isBefore10DaysAgo = lastScreeningDateMoment.isBefore(moment10daysAgo);
  const isBefore14DaysAgo = lastScreeningDateMoment.isBefore(moment14daysAgo);

  const daysAgoLimit10 =
    isBefore10DaysAgo || lastScreeningDate === undefined
      ? "10 days"
      : lastScreeningDateMoment.fromNow(true);

  const daysAgoLimit14 =
    isBefore14DaysAgo || lastScreeningDate === undefined
      ? "14 days"
      : lastScreeningDateMoment.fromNow(true);

  const isDateFirsSymptDisabled = (data: Teams.CovidScreeningFormData) => {
    const hasSymptomps = toggleValueSelector(
      data,
      CovidPaths.SymptomsCheckCovid
    );

    return hasSymptomps !== "yes";
  };

  const isDateOfTestDisabled = (data: Teams.CovidScreeningFormData) => {
    const didTest = toggleValueSelector(data, CovidPaths.TestCheckCovid);

    return didTest !== "yes";
  };

  return (
    <>
      {variant === CovidVariants.Clinician && (
        <FormInputContainer>
          <InputLabel iconName="clinician" title="Clinician confirmation" />
          <Archetypes.CovidConfirmation
            colorVariant="danger"
            path={CovidPaths.ClinicianConfirmation}
          />
        </FormInputContainer>
      )}

      {variant === CovidVariants.Clinician && (
        <FormInputContainer>
          <InputLabel iconName="free-text-solid" title="Clinician Notes" />
          <Archetypes.Notes path={CovidPaths.ClinicianNotesCovid} multiline />
        </FormInputContainer>
      )}

      <FormInputContainer>
        <InputLabel required iconName="calendar" title="Date" />
        <Archetypes.Timestamp
          defaultValue={moment().valueOf()}
          path={CovidPaths.ScreeningDate}
          disableFuture={true}
          validations={{
            required: checkValidInput,
          }}
          title="Date"
        />
      </FormInputContainer>

      <FormInputContainer>
        <InputLabel
          required
          title={`In the last: ${daysAgoLimit10} have you felt unwell?`}
        />
        <Archetypes.YesOrNo
          path={CovidPaths.UnwellCheckCovid}
          validations={{
            required: checkValidInput,
          }}
        />
      </FormInputContainer>

      <FormInputContainer>
        <InputLabel
          required
          title={`In the last: ${daysAgoLimit10} have YOU had any of the following symptoms?`}
        />
        <View style={{ marginBottom: 4 }}>
          <Text type="paragraph" weight="light">
            - Persistent cough
          </Text>
          <Text type="paragraph" weight="light">
            - Fever (&gt; or equal to 37.8C) or suspected fever (temperature)
          </Text>
          <Text type="paragraph" weight="light">
            - Loss of taste or smell
          </Text>
        </View>

        <Archetypes.YesOrNo
          path={CovidPaths.SymptomsCheckCovid}
          validations={{
            required: checkValidInput,
          }}
        />
      </FormInputContainer>

      <FormInputContainer>
        <InputLabel
          iconName="calendar"
          title="Date of the start of FIRST symptoms"
        />
        <Archetypes.Timestamp
          path={CovidPaths.DateOfStartSymptomsCovid}
          title="Date"
          isDisabled={isDateFirsSymptDisabled}
          disableFuture={true}
          validations={{
            required: checkValidInput,
          }}
        />
      </FormInputContainer>

      <FormInputContainer>
        <InputLabel
          required
          title={`In the last: ${daysAgoLimit14} have YOU had a POSITIVE for Covid-19 test?`}
        />
        <Archetypes.YesOrNo
          path={CovidPaths.TestCheckCovid}
          validations={{
            required: checkValidInput,
          }}
        />
      </FormInputContainer>

      <FormInputContainer>
        <InputLabel required iconName="calendar" title="Date of Test" />
        <Archetypes.Timestamp
          path={CovidPaths.TestDate}
          title="Date"
          isDisabled={isDateOfTestDisabled}
          disableFuture={true}
          validations={{
            required: checkValidInput,
          }}
        />
      </FormInputContainer>

      <FormInputContainer>
        <InputLabel
          required
          title={`In the last: ${daysAgoLimit14} have any of your HOUSEHOLD CONTACTS had any of the following symptoms?`}
        />
        <View style={{ marginBottom: 4 }}>
          <Text type="paragraph" weight="light">
            - Persistent cough
          </Text>
          <Text type="paragraph" weight="light">
            - Fever (&gt; or equal to 37.8C) or suspected fever (temperature)
          </Text>
          <Text type="paragraph" weight="light">
            - Loss of taste or smell
          </Text>
        </View>
        <Archetypes.YesOrNo
          path={CovidPaths.HouseHoldSymptomsCheckCovid}
          validations={{
            required: checkValidInput,
          }}
        />
      </FormInputContainer>

      <FormInputContainer>
        <InputLabel
          required
          title={`In the last: ${daysAgoLimit14} have YOU been in contact with a Covid-19 confirmed or suspected case?`}
        />
        <Archetypes.YesOrNo
          path={CovidPaths.ContactWithPositiveCheckCovid}
          validations={{
            required: checkValidInput,
          }}
        />
      </FormInputContainer>

      <FormInputContainer>
        <InputLabel iconName="free-text-solid" title="Notes" />
        <Archetypes.Text path={CovidPaths.NotesCovid} multiline />
      </FormInputContainer>

      <FormInputContainer>
        <Text type="heading2" style={{ marginBottom: 4 }}>
          All questions are in line with NHS guidelines
        </Text>
        <Hyperlink
          style={{ padding: 2 }}
          url={
            "https://hpspubsrepo.blob.core.windows.net/hps-website/nss/3082/documents/1_covid-19-contact-tracing-guidance-hpt.pdf"
          }
          text={"Health Protection Team Guidance"}
        />
        <Hyperlink
          style={{ padding: 2 }}
          url={
            "https://www.nhsinform.scot/self-help-guides/self-help-guide-coronavirus-covid-19"
          }
          text={"NHS inform self-help guide: Coronavirus (COVID-19)"}
        />
      </FormInputContainer>
    </>
  );
};

export { createCovidScreeningForm };
